<template>
  <div class="d-flex justify-center">
      <v-col cols=4>
          <form @submit.prevent="login">

          <v-card elevation="4"> 
              <v-system-bar color="blue darken-2" dark > </v-system-bar>
              <v-card-title>Login</v-card-title>
            <v-container class="pa-7">
                <v-row>
                    <v-col cols=12>
                        <v-text-field
                            v-model="credentials.email"
                            prepend-icon="mdi-email"
                            color="purple darken-2"
                            label="Email"
                            type="email"
                            required
                            >
                        </v-text-field>
                        <v-text-field
                            v-model="credentials.password"
                            prepend-icon="mdi-lock"
                            color="purple darken-2"
                            label="Password"
                            type="password"
                            required
                            >
                        </v-text-field>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols=12 class="d-flex justify-space-between">
                        <v-btn  color="primary" type="submit">Login</v-btn>
                        <v-btn class="ma-1" color="success" to="/signup" plain >
                            Sign Up
                        </v-btn>
                    </v-col>
                </v-row>
                </v-container>
            
            </v-card>
        </form>
      </v-col>
      
  </div>
</template>

<script>
export default {
    data: () => ({
        credentials: {
            email: '',
            password: '',
        }
    }),
    methods: {
        login(){
        this.$auth.login(this.credentials).then(() => {
          this.$router.push('/')
        }).catch(err => {
          this.message = err.response.data.message
        })
      }
    }
}
</script>

