<template>
  <div class="d-flex justify-center">
      <v-col cols=4>
          <form @submit.prevent="signup">

          <v-card elevation="4"> 
              <v-system-bar color="blue darken-2" dark > </v-system-bar>
              <v-card-title>Sign Up</v-card-title>
            <v-container class="pa-7">
                <v-row>
                    <v-col cols=12>
                        <v-text-field
                            v-model="user.name"
                            prepend-icon="mdi-account"
                            color="purple darken-2"
                            label="Name"
                            type="text"
                            required
                            >
                        </v-text-field>
                        <v-text-field
                            v-model="user.email"
                            prepend-icon="mdi-email"
                            color="purple darken-2"
                            label="Email"
                            type="email"
                            required
                            >
                        </v-text-field>
                        <v-text-field
                            v-model="user.password"
                            prepend-icon="mdi-lock"
                            color="purple darken-2"
                            label="Password"
                            type="password"
                            required
                            >
                        </v-text-field>
                        
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols=12 class="d-flex justify-space-between">
                        <v-btn  color="primary" type="submit">Sign Up</v-btn>
                        <v-btn @click.prevent="$router.push('/login')" class="ma-1" color="success" plain >
                            Back to Login
                        </v-btn>
                    </v-col>
                </v-row>
                </v-container>
            
            </v-card>
        </form>
      </v-col>
      
  </div>
</template>

<script>
export default {
    data: () => ({
        user: {
            name: '',
            email: '',
            password: '',
        }
    }),
    methods: {
        signup(){
        this.$auth.signup(this.user).then(() => {
          this.$router.push('/login')
        }).catch(err => {
          console.log(err)
        })
      }
    }
}
</script>

